<template>
    <div v-if="processing_query">Please wait...</div>
    <div v-else-if="results.length==1">
        {{results.length}} result for
        {{search_query}}
    </div>
    <div v-else>
        {{results.length}} results for
        {{search_query}}
    </div>
    <ResultCard
        v-for="result in results"
        :key="result.id"
        :name="result.name"
        :address="result.address"
        :has_tel="result.has_tel"
        :tel="result.tel"
        :has_whatsapp="result.has_whatsapp"
        :whatsapp="result.whatsapp"
        :long="result.long"
        :lat="result.lat"
        :z="result.z"
        :has_gc_website="result.has_gc_website"
        :gc_website="result.gc_website"
    ></ResultCard>   
</template>

<script>
import ResultCard from '../components/ResultCard.vue'
// const red_db = 'http://192.168.0.111/wordpress/'
const red_db = 'https://greencheque.com'

export default {
    name: 'SearchResults',
    components:{
        ResultCard
    },
    props:{
        search_query:String
    },
    data(){
        return{
            results:[],
            processing_query: true,
            isEditing: false
        }
    },
    created: function(){
        this.processing_query = true;
        let domparser = new DOMParser();
        let query_string = red_db + '/wp-json/wp/v2/posts/?search=' + this.search_query;
        fetch(query_string)
            .then(response => response.json())
                .then(data => {              
                    console.log(Object.keys(data).length)
                    if (Object.keys(data).length>0){
                        for(let obj in data){        
                        let x = data[obj].content.rendered;
                        let resp_doc = domparser.parseFromString(x, "text/html");
                        console.log(resp_doc);
                        let y = resp_doc.getElementsByTagName("pre")[0].innerText;
                        let z = JSON.parse(y);
                        this.results.push(z);
                        console.log(z.name);
                        }
                    }else{
                        console.log("no results found");
                    }
                    this.processing_query = false;
                });
    }

//     beforeMount() {
//     window.addEventListener("beforeunload", event => {
//     //   if (!this.isEditing) return
//       event.preventDefault()
//       // Chrome requires returnValue to be set.
//       event.returnValue = ""
//     })
//   }

}
</script>

