<template>
    <div id="nav_main" class="topnav">
        <router-link to="/home" class="active">WID</router-link>
        <!-- <a href="#news" v-on:click="toggleCollapse()">News</a>
        <a href="#contact" v-on:click="toggleCollapse()">Contact</a>
        <router-link to="/about" v-on:click="toggleCollapse()">About</router-link>
        <div id="collapse_icon" href="javascript:void(0);" v-on:click="toggleCollapse()">V</div>         -->
    </div> 
    <div id="nav_main2"></div>  
</template>

<script>
    export default {
        name: 'NavBar',
        methods:{
            toggleCollapse: function () {
                var x = document.getElementById("nav_main");
                if (x.className === "topnav") {
                    x.className += " responsive";
                } else {
                    x.className = "topnav";
                }
            }
        }
    }
</script>

<style scoped>
    body {
        margin: 0px;
        padding: 0px;
        font-family: Arial, Helvetica, sans-serif;
    }

    #collapse_icon {
        float: right;
        display: none;
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 20px;
    }

    #nav_main {
        z-index:1;
        position: fixed;
        top: 0;
        width: 100vw;
        overflow: hidden;
        background-color: #333;
    }

    #nav_main2 {
        z-index:1;
        height: 10vh;
    }
  
  
    #nav_main a {
        float: left;
        display: block;
        color: #f2f2f2;
        text-align: center;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 1rem;
    }

    #nav_main a:not(:first-child){
        float: right;
        display: block;
    }

    #nav_main a:hover {
        background-color: #ddd;
        color: black;
    }

    @media screen  and (max-width: 600px){
        #collapse_icon{
            position: absolute;
            top: 0%;
            right: 0%;
            display: block;
            color:aqua;
        }
        #nav_main a:not(:first-child) {
            display: none;
        }
        #nav_main.responsive a {
            float: none;
            display: block;
            text-align: left;
        }
    }
</style>