<template>
    <div class="result_card">
        <h1>{{name}}</h1>
        <h3>{{address}}</h3>
        <div v-if="hasTel">
            <a :href="telText">
                <div class="result_card_phone"></div>
                <h3>{{tel}}</h3>
            </a>
        </div>
        <div v-if="hasWhatsapp">
            <a :href="whatsappText" target="_blank">
                <div class="result_card_whatsapp"></div>
                <h3>{{tel}}</h3>
            </a>
        </div>
        <div v-if="hasLocation">
            <a :href="locationLink" target="_blank">
                <div class="result_card_location"></div>
                <h3>Google Maps</h3>
            </a>
        </div>
        <div v-if="hasGcSite">
            <a :href="gcWebsite" target="_blank">
                <div class="result_card_web"></div>
                <h3>Visit GC Site</h3>
            </a>
        </div>
    </div>    
</template>

<script>
export default {
    name: 'ResultCard',
    props: {
        id: Number,
        name: String,
        address: String,
        has_tel: String,
        tel: String,
        has_whatsapp: String,
        whatsapp: String,
        long: String,
        lat: String,
        z: String,
        has_gc_website: String,
        gc_website: String,
        sells_online: String
    },
    computed:{
        telText(){
            return "tel:" + this.tel;
        },
        whatsappText(){
            return "https://wa.me/" + this.tel;
        },
        hasGcSite(){
            console.log(this.has_gc_website);
            if(this.has_gc_website){
                switch(this.has_gc_website.toLowerCase().trim()){
                    case "true": case "yes": case "1": return true;
                    case "false": case "no": case "0": case null: return false;
                    default: return Boolean(this.has_gc_website);
                }
            }
            else return false;
        },
        hasLocation(){
            if(this.long & this.lat){
                return true;
            }
            else return false;
        },
        hasTel(){
            if(this.has_tel){
                switch(this.has_tel.toLowerCase().trim()){
                    case "true": case "yes": case "1": return true;
                    case "false": case "no": case "0": case null: return false;
                    default: return Boolean(this.has_tel);
                }
            }
            else return true;
        },
        hasWhatsapp(){
            if(this.has_whatsapp){
                switch(this.has_whatsapp.toLowerCase().trim()){
                    case "true": case "yes": case "1": return true;
                    case "false": case "no": case "0": case null: return false;
                    default: return Boolean(this.has_whatsapp);
                }
            }
            else return true;
        },
        gcWebsite(){
            if (this.hasGcSite){
                return this.gc_website;
            }
            else return "/"

        },
        locationLink(){
            return "https://www.google.com/maps/@" + this.long + "," + this.lat + "," + this.z;
        }
    }
}
</script>

<style scoped>
*{
    margin: 0px;
    padding: 0px;
}
.result_card{
    width: 90vw;
    height: auto;
    margin: 10px auto 30px auto;
    border: 1px solid gray;
    border-radius: 10px;
}

.result_card>div{
    display: flex;
    flex-direction: row;
    padding: 0px;
    /* border: 1px solid green; */
    padding: 10px 0px 10px 0px;
}

.result_card>div>a{
    color: rgb(230, 92, 0);
    text-decoration: none;  
    display: flex;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;
}

.result_card>div>a>h3{
    /* color: rgb(0,40,40); */
    margin-left: 5px;
}

.result_card>h1{
    color:honeydew;
    background-color: rgb(1, 102, 1);
    border-radius: 10px 10px 0px 0px;
}

.result_card>h3{
    color: rgb(40,40,40);
    margin-left: 5px;
    padding: 10px 0px 10px 0px;
}

.result_card_location{
    min-height: 20px;
    min-width: 20px;
    background-image: url('../assets/svg_assets/google-maps.svg');
    background-repeat: no-repeat;
    background-position: center;
    filter: invert(27%) sepia(51%) saturate(2878%) hue-rotate(346deg) brightness(104%) contrast(97%);
}

.result_card_phone{
    min-height: 20px;
    min-width: 20px;
    background-image: url('../assets/svg_assets/phone.svg');
    background-repeat: no-repeat;
    background-position: center;
}

.result_card_web{
    min-height: 20px;
    min-width: 20px;
    background-image: url('../assets/svg_assets/web.svg');
    background-repeat: no-repeat;
    background-position: center;
    filter: invert(22%) sepia(55%) saturate(2950%) hue-rotate(186deg) brightness(93%) contrast(103%);
}

.result_card_whatsapp{
    min-height: 20px;
    min-width: 20px;
    background-image: url('../assets/svg_assets/whatsapp.svg');
    background-repeat: no-repeat;
    background-position: center;
    filter: invert(40%) sepia(22%) saturate(2536%) hue-rotate(81deg) brightness(97%) contrast(81%);
}


</style>